import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

import Typography from '@material-ui/core/Typography'

import ModelConfirm from './ModelConfirm'

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(1),
    display: 'flex',
    overflow: 'auto',
    width: '100%',
    flexDirection: 'column',
  },
  button: {
    margin: theme.spacing(1),
  },
  link: {
    textDecoration: 'none',
    color: 'dodgerblue',
    display: 'flex',
    alignItems: 'center',
  },
}))

const OrderTable = (props) => {
  const classes = useStyles()
  const { data } = props

  return (
    <Grid item xs={12}>
      <Paper className={classes.paper}>
        <Table size="medium">
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>Email</TableCell>
              <TableCell>School</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data && data.length > 0 ? (
              data.map((userData) => (
                <TableRow>
                  <TableCell>{`${userData.firstName} ${userData.lastName}`}</TableCell>
                  <TableCell>{userData.email}</TableCell>
                  <TableCell>{userData.school}</TableCell>
                  <Table size="small">
                    <TableHead>
                      <TableCell>Received</TableCell>
                      <TableCell>Product Name</TableCell>
                      <TableCell>Product Model</TableCell>
                      <TableCell>Quantity</TableCell>
                      <TableCell>Note</TableCell>
                      <TableCell>Expected Date</TableCell>
                    </TableHead>
                    <TableBody>
                      {userData.currentlyLending &&
                      userData.currentlyLending.length > 0
                        ? userData.currentlyLending.map((product) => (
                            <TableRow>
                              <TableCell>
                                <ModelConfirm
                                  userId={userData.id}
                                  productId={product.id}
                                  userName={`${userData.firstName} ${userData.lastName}`}
                                  productName={product.productName}
                                  productModal={product.model}
                                  quantity={product.quality}
                                ></ModelConfirm>
                              </TableCell>
                              <TableCell>{product.productName}</TableCell>
                              <TableCell>{product.model}</TableCell>
                              <TableCell>{product.quality}</TableCell>
                              <TableCell>{product.note}</TableCell>
                              <TableCell>
                                {new Date(product.lendTime).toDateString()}
                              </TableCell>
                            </TableRow>
                          ))
                        : null}
                    </TableBody>
                  </Table>
                  <TableCell></TableCell>
                </TableRow>
              ))
            ) : (
              <Typography
                component="h3"
                variant="h3"
                color="primary"
                gutterBottom
              >
                No data to display
              </Typography>
            )}
          </TableBody>
        </Table>
      </Paper>
    </Grid>
  )
}

export default OrderTable
