import React from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import Slide from '@material-ui/core/Slide'
import IconButton from '@material-ui/core/IconButton'
import ClearIcon from '@material-ui/icons/Clear'
import Typography from '@material-ui/core/Typography'
import firebase from 'firebase'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />
})

export default function AlertDialogSlide(props) {
  const [open, setOpen] = React.useState(false)

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleYesClciked = () => {
    const { userId, index } = props
    let data
    console.log(index)

    let ref = firebase.firestore().collection('reserves').doc(userId)

    ref
      .get()
      .then(function (doc) {
        data = doc.data().reserves
      })
      .then(() => {
        data.splice(index, 1)
        console.log(data)
        ref.update({
          reserves: data,
        })
        handleClose()
      })
    refreshPage()
  }

  function refreshPage() {
    // window.location.reload()
  }

  return (
    <div>
      <IconButton color="primary" component="span" onClick={handleClickOpen}>
        <ClearIcon></ClearIcon>
      </IconButton>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">{'Remove:'}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            <Typography
              component="h5"
              variant="h5"
              color="primary"
              gutterBottom
            >
              {props.productName} from <strong>{props.userName}</strong>
            </Typography>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            NO
          </Button>
          <Button onClick={handleYesClciked} color="primary">
            YES
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
