import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import ProductsTable from './AdminTable'
import firebase from 'firebase'
import ReserveTable from './ReserveTable/ReserveTable'

const AdminUserManager = (props) => {
  const [userData, setUserData] = useState([])
  const [alreadyGotData, setAlreadyGotData] = useState(false)
  const [reserveData, setReserveData] = useState()

  let tempData = []

  const getData = async () => {
    let tempUserData = []

    let usersRef = firebase.firestore().collection('users')
    let allUsers = usersRef
      .get()
      .then((snapshot) => {
        snapshot.forEach((doc) => {
          if (
            doc.data().currentlyLending !== undefined &&
            doc.data().currentlyLending.length > 0
          ) {
            const id = doc.id
            let tempUserData = doc.data()
            tempUserData.id = id
            tempData.push(tempUserData)
            setUserData(tempData)
          }
        })
      })
      .catch((err) => {
        console.log('Error getting documents', err)
      })
  }

  const getUserInfo = async (userID) => {}

  const getReserveData = async () => {
    let tempReservesData = []

    let reservesRef = firebase.firestore().collection('reserves')
    let allReserves = reservesRef
      .get()
      .then((snapshot) => {
        snapshot.forEach((doc) => {
          if (
            doc.data().reserves !== undefined &&
            doc.data().reserves.length > 0
          ) {
            const id = doc.id
            let tempUserData = doc.data().reserves
            tempUserData.uid = doc.id

            let docRef = firebase.firestore().collection('users').doc(id)
            docRef
              .get()
              .then(function (doc) {
                if (doc.exists) {
                  if (doc.data()) {
                    let data = doc.data()
                    data.reserves = tempUserData
                    tempReservesData.push(data)
                  }
                } else {
                  console.log('No such document!')
                }
              })
              .catch(function (error) {
                console.log('Error getting document:', error)
              })
          }
        })
      })
      .then(() => {
        setReserveData(tempReservesData)
      })
      .catch((err) => {
        console.log('Error getting documents', err)
      })
  }

  useEffect(() => {
    if (alreadyGotData != true) {
      getData()
      getReserveData()
      setAlreadyGotData(true)
    }
  }, [])

  if (alreadyGotData) {
    return (
      <Grid item xs={12}>
        <Typography component="h3" variant="h5" color="primary" gutterBottom>
          Order Details
        </Typography>
        <ProductsTable data={userData} />
        <Typography
          style={{ marginTop: 30 }}
          component="h3"
          variant="h5"
          color="primary"
          gutterBottom
        >
          Reserve Details
        </Typography>
        <ReserveTable data={reserveData}></ReserveTable>
      </Grid>
    )
  } else {
    return null
  }
}

AdminUserManager.propTypes = {
  products: PropTypes.array,
}

const mapStateToProps = (state) => {
  const {
    firestore: {
      ordered: { products },
    },
  } = state
  return {
    products,
  }
}

export default connect(mapStateToProps)(AdminUserManager)
